    <div class="col-xl-12 col-12 content">
      <div class="col-lg-12 col-12">
        <div class="title">
          <h3 class="border_line">廟宇特色</h3>
        </div>
      </div>
      <div class="col-lg-12 col-12">
        <div class="title">
            <div class="col-lg-12 col-12 vam">
                <div class="col-lg-2 col-3">
                    <img class="turkey_img no_rotate" src="../assets/picture/turkey.png">
                </div>
                <div class="col-lg-10 col-12">
                    <p class="t-left">城隍廟，古名「諸羅城隍廟」，是嘉義市民的信仰中心，自古就與民眾息息相連，香火鼎盛；嘉義城隍廟於清治時期在台灣府諸羅縣官府最初創建的城隍廟，既是諸羅城三大古廟之一，也是繼台灣府城隍廟、台灣縣城隍廟後為台灣第三座建立的城隍廟，是不是迫不及待想參訪看看呢?</p>
                </div>
            </div>
        </div>
        <div class="col-lg-12 col-12 vam">
            <div class="col-lg-6 col-12">
                <p class="temple_structure">現今的城隍廟大多是以日治時期聘請到福建泉州大匠師王錦木來規劃設計，主體座向「座東朝西」，廟體建築風格「泉州溪底派」。正殿、後殿夾拜亭之「工」字形平面建築格局。整體建築共分三川殿、拜亭、正殿、後殿四大部份，以前殿作為拜亭與正殿的統稱，其最大特色是在正殿部份安設許多看架斗栱，請不忘看看這精細的技藝，屋頂形式皆為重簷，巧奪天工，莊嚴肅穆；大家來到城隍廟，仔細欣賞一下這有如鬼斧神工的藝術，真令人驚嘆不已~</p>
            </div>
            <div class="col-lg-6 col-12">
                <div class="col-lg-12 col-12">
                    <img class="w-100" src="../assets/picture/IMG_20211111_104710.jpg">
                </div>
            </div>
        </div>
      </div>
    </div>
    <div class="col-xl-12 col-12 vam content">
        <div class="col-lg-12 col-12">
          <div class="col-lg-6 col-6">
              <div class="title">
                  <h3 class="border_line">交趾陶技藝</h3>
              </div> <!---->
          </div>
          <div class="col-lg-6 col-12">
            <div class="foot_explanation" v-if="show_card == true">
                <p>交趾陶又名嘉義燒，是華南與臺灣傳統建築（如廟宇）的裝飾，源自唐三彩，是一種低溫彩釉軟陶，主要都在民間默默發展與延續，現在依然可在廟宇內欣賞到交趾陶，後來創作題材受限於傳統，少有新意，遂逐漸沒落。</p>
            </div>
          </div>
        </div>
        <div class="col-lg-12 col-12 vam">
            <div class="col-lg-12 col-12">
                <!-- <div class="col-lg-9 col-9">

                </div> -->
                <div class="col-lg-9 col-8">
                    <p>嘉義城隍廟，由兩位大師林添木、 陳專友各在拜亭的左右兩側展現自己交趾陶的技藝，特別是在拜殿「至誠前知」匾額下方也分屬兩人對場之作，造就出交趾陶界有名「拚場」，由陳專友的《群仙會》對林添木的《八仙祝壽》，是罕見的技藝拚場，作品細膩傳神、價值非凡，這場技藝作品的比拚也是絕無僅有，已傳為佳話。</p>
                </div>
                <div class="col-lg-2 col-4">
                    <img class="turkey_image feet no_rotate" :class="click_me" @click="show_card = true ;click_me='no'" src="../assets/picture/turkey.png">
                </div>
                <div class="col-lg-12 col-12 no-padding xl-no-padding two_feet">
                    <div class="col-lg-4 col-12">
                        <img src="../assets/picture/虎門.jpg">
                        <p class="mt-5">虎門交趾陶</p>
                        <p>林添木，虎邊天井水車堵由內而外分別為&lt;&lt;狸貓換太子&lt;&lt;、&lt;&lt;古城會&lt;&lt;、&lt;&lt;伯邑考進貢&lt;&lt;，以三則故事為題材所做出的作品</p>
                    </div>
                    <div class="col-lg-4 col-12">
                        <img src="../assets/picture/龍門.jpg">
                        <p class="mt-5">龍門交趾陶</p><p>陳專友，龍邊天井水車堵由內而外分別是&lt;&lt;甘露寺召親&gt;&gt;、&lt;&lt;泗洲城&gt;&gt;、&lt;&lt;連環計&gt;&gt;，以道教神話故事為題材</p>
                    </div>
                    <div class="col-lg-4 col-12">
                        <img src="../assets/picture/拚場.jpg">
                        <p class="mt-5">「拚場」交趾陶</p>
                        <p>由陳專友的&lt;&lt;群仙會&gt;&gt;對林添木的&lt;&lt;八仙祝壽&gt;&gt;，是絕無僅有的珍寶之一</p>
                    </div>
                </div>
            </div>
        </div> <!---->
    </div>
    <div class="col-xl-12 col-12 content">
