                </div>
                <p class="mt-20">步行:</p>
                <p class="mt-20">下嘉義火車站由中山路，東往仁愛路至中正路口，直走至吳鳳北路口，轉往南方向約100公尺可達本中心。</p>
                <p class="mt-20">公車:</p>
                <p class="mt-20">1.抵達嘉義火車站後，搭乘市區公車「光林我嘉線」至「嘉邑城隍廟站」下車。</p>
                <p class="mt-20">2.抵達嘉義火車站後，搭乘市區公車「中山幹線」至「市政府站」下車，走至吳鳳北路後往南方向步行約4分鐘。</p>
            </div>
            <div class="col-lg-6 col-12 traffic_info no-padding">
                <div class="col-lg-12 col-12 no-padding-left xl-no-padding-left">
