                </div>
                <p class="mt-15">搭乘BRT與公車轉運:</p>
                <p class="mt-15">1.嘉義高鐵站下車後，搭乘BRT至民族停車場站下車，步行約9分鐘。</p>
                <p class="mt-10">2.嘉義高鐵站下車後，搭乘BRT至「嘉義市轉運中心」轉搭乘市區公車「光林我嘉線」至「嘉邑城隍廟站」下車。</p>
                <p class="mt-10">3.嘉義高鐵站下車後，搭乘BRT至「嘉義市轉運中心」轉搭乘市區公車「中山幹線」至「市政府站」下車，走至吳鳳北路後往南方向步行約4分鐘。</p>
