<script>
// import image from '../@/assets/'
import hamburger from '@/components/hamburger.vue'
import Vue from 'vue'
import VuePageTitle from 'vue-page-title'
Vue.use(VuePageTitle, {
  // prefix: 'My App - ',
  // suffix: '歷史軌跡'
})
Vue.directive('click-outside', {
  bind (el, binding, vnode) {
    console.log(el)
    window.event = function (event) {
      // eslint-disable-next-line eqeqeq
      if (!(el == event.target ||
      el.contains(event.target) ||
      event.target.className === 'triangle_on_img' ||
      event.target.className.includes('turkey_image') ||
      event.target.className.includes('surprise') ||
      event.target.className.includes('triangle_in_history_evolution') ||
      event.target.className.includes('triangle'))) {
        vnode.context[binding.expression](event)
      }
    }
    document.body.addEventListener('click', window.event)
  },
  unbind: function (el) {
    document.body.removeEventListener('click', window.event)
  }
})
export default {
  name: 'temple_explore',
  title:"城隍廟探奇",
  components: {
    hamburger
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      card: '',
      card1: '',
      card2: '',
      show: 'none',
      show1: 'none',
      show2: 'none',
      show3: 'none',
      click_me:"click_me",
      show_card : false,
      dark_color_div:false,
    }
  },
  methods: {
    go_fullscreen(){
      this.$refs.fullscreen.requestFullscreen();
      this.$refs.embedVR.style.height = window.screen.height + "px";
      // this.Element.remove();
    }
  }

}
